import React from 'react';
import styled from 'styled-components';
import wawelImage from './wawel.jpg';
import wawelImage2 from './wawel2.jpg';
import Card from './Card';
import Data from './Data';

class Cards extends React.Component {
  state = {
    cards: [...Data],
    modal: false,
    modalContent: {
      title: 'Lorem Ipsum',
      text: 'Lorem Ipsum',
    },
  };
  handleModal = async (title, text) => {
    await this.setState({ modal: !this.state.modal });
    await this.setState({ modalContent: { title, text } });
  };
  handleCloseModal = async () => {
    await this.setState({ modal: false });
  };
  render() {
    return (
      <>
        {this.state.modal && (
          <Modal>
            <ToolBar>
              <H2>{this.state.modalContent.title}</H2>
              <Button onClick={this.handleCloseModal}>x</Button>
            </ToolBar>
            <Text>{this.state.modalContent.text}</Text>
          </Modal>
        )}
        <CardWrapper>
          {this.state.cards.map((item) => (
            <Card
              func={this.handleModal}
              icon={item.icon}
              title={item.title}
              image={item.image}
              text={item.text}
            />
          ))}
        </CardWrapper>
      </>
    );
  }
}

export default Cards;

const Text = styled.p`
  padding: 2vw 2vw 2vw 2vw;
  font-size: 2vw;
  @import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
  font-family: 'Roboto', sans-serif;
  text-align: center;
`;

const Button = styled.div`
  width: 15%;
  height: 100%;
  border: none;
  background-color: transparent;
  font-size: 2.5vw;
  outline: none;
  cursor: pointer;
  color: white;
  text-align: center;
  transition: background-color 0.5s;
  :hover {
    background-color: #e81a07;
  }
`;

const H2 = styled.h2`
  @import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
  margin: 0;
  font-size: 2vw;
  color: white;
  padding-left: 2vw;
  width: 32vw;
  display: block;
  max-width: 32vw;
  align-self: center;
  font-family: 'Roboto', sans-serif;
`;

const Modal = styled.div`
  position: fixed;
  z-index: 100;
  width: 40vw;
  height: 26vw;
  background-color: whitesmoke;
  transform: translateX(30vw) translateY(25vh);
  border: none;
`;

const ToolBar = styled.div`
  width: 100%;
  height: 15%;
  background-color: #454b75;
  display: flex;
  border: none;
`;

const CardWrapper = styled.div`
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url(${wawelImage});
  background-size: cover;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  justify-items: center;
`;
