import React, { useEffect, useRef } from 'react';
import styled from 'styled-components';
import { useHistory } from 'react-router-dom';
import Title from './Title';
import TitleButton from './TitleButton';
import Subtitle from './Subtitle';
import wawelImage from './wawel.jpg';
import './Home.css';

const Home = () => {
  let history = useHistory();
  const pushHistory = () => {
    history.push('/wawel/cards');
  };
  return (
    <Div>
      <Title className='h1'>Wawel Castle</Title>
      <TitleButton onClick={pushHistory} className='b1'>
        Next
      </TitleButton>
      <Subtitle className='h2'>Krystian Zieja 2i</Subtitle>
    </Div>
  );
};

export default Home;

const Div = styled.div`
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url(${wawelImage});
  background-size: cover;
`;
